.title {
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 2px;
  height: 26px;
}

.divide {
  cursor: pointer;
  color: #1890ff;
}
.divide:hover {
  color: #40a9ff;
}
.ant-table-thead > tr > th {
  text-align: center !important;
  padding: 2px !important;
}

.ant-table-tbody > tr > td {
  text-align: center !important;
  overflow-wrap: break-word !important;
}

.ant-table-thead > tr > th > .ant-input {
  border-radius: 0;
}

.ant-table-header-column {
  width: 100% !important;
}

.list-task {
  text-align: center;
  font-size: 24px;
  font-weight: 500;
}
